*
{
    margin: 0;
    padding: 0;
}
.home
{
    background-image: url('https://img.freepik.com/free-photo/medicine-blue-background-flat-lay_23-2149341573.jpg?size=626&ext=jpg&ga=GA1.1.1224184972.1714176000&semt=ais');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 300px;
}
.home_title
{
    color:rgb(128, 0, 70);
    font-size: 30px;
}
.home_container
{
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.home_formcontainer
{
    box-shadow: 1px 4px 12px -2px rgba(0, 0, 0, 4.3);
    background: transparent;
    padding: 30px;
    border-radius: 10px;
    width: 500px;
}
.home_formcontainer input
{
    border: 1px solid black;
    outline: none;
    padding: 15px 10px;
    width: 80%;
    border-radius: 8px;
    margin: 10px 0;
    background: transparent;
    color: darkmagenta;
    font-size: 14px;
}
.home_formcontainer button
{
    background: red;
    border: 1px solid black;
    outline: none;
    font-size: 18px;
    color: black;
    padding: 10px;
    width: 200px;
    margin-top: 30px;
    margin-left: 30px;
    border-radius: 12px;
    cursor: pointer;
    align-items: center;

}
